import * as React from "react"

import Layout from "../components/layout"
import { Link } from "gatsby";

const NotFoundPage = () => (
  <Layout>
    <h1 className="text-4xl font-bold mb-4 border-b-4 border-brand inline-block pr-6 pb-1">Страница не найдена</h1>
    <p className="text-base">Попробуйте вернуться на <Link to="/" className="text-brand border-dashed border-brand border-b">Главную</Link></p>
  </Layout>
)

export default NotFoundPage
